.navContainer{
    padding: 17px;

    .signInBtn{
        margin-left: auto;
    }
}


.mainPageImg{
    background: url('https://images.unsplash.com/photo-1493219686142-5a8641badc78?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60') no-repeat center;
    height: 630px; 
    background-size: cover;
    width: 100%; 
    position: relative;
    margin-top: 72px;

    .imgContent{
        text-align: center; 
        
        h1{
            position: absolute;
            top: 35%;
            left: 30%;
        }
        p{
            position: absolute;
            top:42%;
            left: 30%;
            width: 398px;
            font-size: 23px;
        }
    }
}

.containerContent{
    margin-top: 40px;

    .cardContainer{
        margin-bottom: 30px;

        .cardContent{
            width: 400px;
            margin-bottom: 30px;

            .cardImg{
                width: 100%;
            }
        }
    }

    .notCreatedContent{
        padding: 40px;
        font-size: 30px;
    }
}

@media screen and (min-width: 480px) {
    .imgContent{
        text-align: center; 
    
    h1{
        position: absolute;
        top: 35%;
        left: 30%;
    }
    p{
        position: absolute;
        top: 42%;
        left: 30%;
        width: 220px;
        font-size: 17px;
    }
    }
}

.carousel-inner img {
    width: 100%;
    height: 100%;
}

.card-body{
    max-height: none;
}