.navbarContainer{
    padding: 17px;

    .signInBtn{
        margin-left: auto;
    }
}

.expandBlogContainer{
    margin-top: 100px;
    width: 730px;
    box-sizing: border-box;

    .blogImg{
        margin-top: 35px;
        display: block;
    }

    .blogContent{
        margin-top: 30px;
        text-align: justify;
        font-family: Merriweather;
        font-weight: 400;

        .smallDesc, .blogDescription{
            font-size: 20px;
        }
    }
}

.footer{
    background-color: lightgrey;
    padding: 20px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
}