.navbarContent{
    padding: 17px;

    .profileDropdown{
        margin-left: auto;

        .roundProfileBtn{
            cursor: pointer;
            width: 38px;
        }
    }
}

.blogContainer{
    margin: 33px auto auto;
    max-width: 100%;
    box-sizing: border-box;
    flex-flow: row wrap;

    .containerHeading{
        align-items: center;
        justify-content: space-between;
        margin-bottom: 33px;
    }

    .cardContainer{
        margin-bottom: 30px;

        .blogCard{
            width: 400px;
            margin-bottom: 30px;

            .cardImg{
                width: 100%;
            }

            .cardEditBtn{
                margin-left: 18%;
            }
        }
    }
    .nothingCreatedContent{
        padding: 40px;
        font-size: 30px;
    }
}